.Tile-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
  position: relative;
  font-size: 14px;
  padding: 15px;
}

.Tile-container > img {
  position: absolute;
  top: 10px;
  right: 10px;
}

.Tile-ellipsis {
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.Tile-title-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.Tile-title {
  font-size: 16px;
  font-weight: bold;
  max-width: 185px;
}

.Tile-url {
  margin-top: 8px;
  width: 280px;
}

.Tile-author {
  width: 210px;
}

.Tile-container-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 100%;
}

.Tile-row {
  display: -ms-flexbox;
  display: flex;
  margin-top: 8px;
}

.Tile-row-title {
  font-weight: bold;
  width: 60px;
}

.Tile-text {
  vertical-align: middle;
  line-height: 18px;
}

.Tile-tags-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.Tile-icon  > * > button {
  padding: 0;
}



